
































































  import {
    mapState,
    mapMutations
} from 'vuex'

export default {
    computed: {
        ...mapState(['isLogin', 'forcedLogin','data'])
    },
	data() {
		return {
			providerList:[],
			avatarUrl1:"../../../../static/logo.png",
			ava:'../../../../static/dog.png'
		}
	},
	
    methods: {
        ...mapMutations(['logout']),
		getnew(){
			uni.showModal({
				title: '已是最新版本~_~',
				content: '886^~^',
			
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		tophone(){
			uni.showToast({
				icon:'none',
				title: '暂未开放',
				mask: false,
				duration: 1500
			});
		},
		feedback(){
			uni.navigateTo({
				url: '../../../publish/uni-feedback',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		
		
       bindLogin() {
           if(!this.isLogin){
				uni.navigateTo({
					url:"../../../user/login/login"
				})
			}
        },
        bindLogout() {
			
            this.logout();
            /**
             * 如果需要强制登录跳转回登录页面
             */
            if (this.forcedLogin) {
                uni.reLaunch({
                    url: '../../../user/login/login',
                });
            }
        },
		goAbout() {










		},
		setup(){
			uni.showToast({
				icon:'none',
				title: '暂无设置',
				mask: false,
				duration: 1500
			});
		},
		toshare(e) {
			if (this.providerList.length === 0) {
				uni.showModal({
					title: '当前环境无分享渠道!',
					showCancel: false
				})
				return;
			}
			let itemList = this.providerList.map(function (value) {
				return value.name
			})
			uni.showActionSheet({
				itemList: itemList,
				success: (res) => {
					uni.share({
						provider: this.providerList[res.tapIndex].id,
						scene: this.providerList[res.tapIndex].type && this.providerList[res.tapIndex].type === 'WXSenceTimeline' ? 'WXSenceTimeline' : "WXSceneSession",
						type: 0,
						title:'欢迎体验WHUER',
						summary: 'WHUER 使用 uni-app 开发',
						imageUrl:'https://img-cdn-qiniu.dcloud.net.cn/uploads/nav_menu/8.jpg',
						href:"https://m3w.cn/uniapp",
						success: (res) => {
							console.log("success:" + JSON.stringify(res));
						},
						fail: (e) => {
							uni.showModal({
								content: e.errMsg,
								showCancel:false
							})
						}
					});
				}
			})
		}
    },
	onLoad() {
		this.version = plus.runtime.version;
		uni.getProvider({
			service: 'share',
			success: (e) => {
				let data = [];
				for (let i = 0; i < e.provider.length; i++) {
					switch (e.provider[i]) {
						case 'weixin':
							data.push({
								name: '分享到微信好友',
								id: 'weixin'
							})
							data.push({
								name: '分享到微信朋友圈',
								id: 'weixin',
								type: 'WXSenceTimeline'
							})
							break;
						case 'qq':
							data.push({
								name: '分享到QQ',
								id: 'qq'
							})
							break;
						default:
							break;
					}
				}
				this.providerList = data;
			},
			fail: (e) => {
				console.log('获取登录通道失败'+ JSON.stringify(e));
			}
		});
	}
}

